.App {
    text-align: center;
    }
    .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear; height: 80px;
    }
    .App-header { background-color: #222; height: 150px;
    padding: 20px;
    color: white;
    }
    .App-title { font-size: 1.5em;
    }
    .App-intro { font-size: large;
    }
    @-webkit-keyframes App-logo-spin { from { -webkit-transform: rotate(0deg); transform: rotate(0deg); } to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    }
    @keyframes App-logo-spin { from { -webkit-transform: rotate(0deg); transform: rotate(0deg); } to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
    }
    .centerize { width: 400px; height: 100px;
    position: absolute; top:0;
    bottom: 0;
    left: 0;
    right: 0; margin: auto;
    }
    .content {
    display: inline-block; padding: 20px;
    }
    h2{
    text-align: center;
    }
  
  .App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
  }
  
  .App-link {
    color: #61dafb;
  }
  
  @keyframes App-logo-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  


